body {
	margin: 0;
	max-width: 100%;
	font-size: 16px;
	font-family: sans-serif;
	background: #f2f2f2;
}


/* ===== Header ===== */
	header {
		width: 25%;
		height: 100%;
		position: fixed;
		transition: 0.3s;
		background-color: #222222;
	}

	.site-title {
		color: white;
		text-align: center;
		margin-bottom: 30px;
	}

	.site-title h1 {
		margin-bottom: 0.5rem;
	}

	.menu-container-mobile {
		display: none;
		padding: 20px;
	}

	.menu-container-mobile div {
		width: 100%;
		height: 100px;
		display: flex;
		color: white;
		line-height: 100px;
		border-radius: 10px;
		background-color: #dd4c39;
	}

	.menu-container-mobile div p {
		margin: 0;
		width: 93%;
		font-size: 30px;
		padding-left: 20px;
	}

	.menu-container-pc {
		padding: 5%;
	}

	.menu-container-pc div {
		height: 70px;
		display: flex;
		color: white;
		transition: 0.3s;
		line-height: 70px;
		border-left: 2px solid #222222;
		border-bottom: 1px solid rgba(255,255,255,.1)
	}

	.menu-container-pc div:hover {
		cursor: pointer;
		background-color: #47474793;
		color: rgba(223, 46, 46, 0.884);
		border-left: 2px solid rgba(223, 46, 46, 0.884);
	}

	.menu-container-pc div p {
		margin: 0;
		width: 95%;
		padding-left: 20px;
	}

	.site {
		text-align: center;
	}

	.site img {
		width: 30px;
		margin: 5px;
		border-radius: 5px;
		margin-bottom: 30px;
	}

	.site img:hover {
		cursor: pointer;
	}
/* ===== Header ===== */


/* ===== Content ===== */
	.content-wrap {
		max-width: 75%;
		display: flex;
		padding: 20px;
		margin-left: 25%;
	}

	.content-wrap .left {
		width: 75%;
		margin-right: 20px;
		border-radius: 10px;
		background-color: white;
	}

	.content-wrap .right {
		width: 25%;
		overflow: hidden;
	}
/* ===== Content ===== */


/* ===== Recent Post ===== */
	.right .recent-post {
		height: 400px;
		padding: 20px;
		margin-bottom: 20px;
		border-radius: 10px;
		background-color: white;
	}

	.recent-post .title {
		margin-bottom: 10px;
		border-bottom: 2px solid rgba(0,0,0,.05);
	}

	.recent-post .list div {
		height: 60px;
		line-height: 60px;
		color: rgba(223, 46, 46, 0.884);
		border-bottom: 1px solid rgba(0,0,0,.05);
	}

	.recent-post .list div:hover {
		cursor: pointer;
		font-weight: bold;
		color: #222222;
	}

	.title h2 {
		margin-top: 0;
		font-weight: 700;
		margin-bottom: -2px;
		display: inline-block;
		border-bottom: 2px solid rgba(223, 46, 46, 0.884)
	}
/* ===== Recent Post ===== */


/* ===== Instagram ===== */
	.right .instagram {
		height: 420px;
		padding: 10px;
		border-radius: 10px;
		background-color: white;
	}

	.instagram .title {
		height: 40px;
		display: flex;
		line-height: 40px;
	}

	.instagram .title img {
		width: 30px;
		height: 30px;
		margin-top: 5px;
		margin-left: 5px;
		border-radius: 50%;
	}

	.instagram .title p {
		font-weight: bold;
		margin: 0 0 0 15px;
	}

	.instagram .insta-img {
		height: 330px;
		margin-top: 15px;
		text-align: center;
		border-bottom: 1px solid rgba(0,0,0,.05);
	}

	.instagram .insta-img img {
		width: 310px;
		border-radius: 10px;
	}

	.instagram .insta-footer {
		height: 45px;
		line-height: 45px;
		padding-left: 8px;
	}

	.instagram .insta-footer span {
		margin-right: 20px;
	}

	.instagram .insta-footer span:hover {
		cursor: pointer;
		color: grey;
	}
/* ===== Instagram ===== */